import { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
// swal
import swal from 'sweetalert';
// @mui
import {
    Box,
    Card,
    Grid,
    Alert,
    Stack,
    Typography,
    Switch,
    FormControlLabel,
    CircularProgress,
} from '@mui/material';
import axios from '../../utils/axios';

// ----------------------------------------------------------------------
VirtualAccountLock.propTypes = {
    setting: PropTypes.object,
};

export default function VirtualAccountLock({ setting }) {
    const [loading, setLoading] = useState(true);
    const [saving, setSaving] = useState(false);
    const [providers, setProviders] = useState({
        palmpay: { enabled: true, is_default: false },
        monnify: { enabled: true, is_default: false },
        wema: { enabled: true, is_default: false },
        xixapay: { enabled: true, is_default: false },
    });
    const [error, setError] = useState(null);

    const AccessToken = window.localStorage.getItem('accessToken');

    // Fetch current status
    useEffect(() => {
        fetchStatus();
    }, []);

    const fetchStatus = async () => {
        try {
            setLoading(true);
            const response = await axios.get('/api/secure/virtualaccounts/status');
            if (response.data.status === 'success') {
                setProviders(response.data.providers);
            }
        } catch (err) {
            console.error('Error fetching virtual account status:', err);
            setError('Failed to load virtual account settings');
        } finally {
            setLoading(false);
        }
    };

    const handleToggle = async (provider) => {
        const newEnabled = !providers[provider].enabled;

        // Check if trying to disable all providers
        const enabledCount = Object.values(providers).filter((p) => p.enabled).length;
        if (!newEnabled && enabledCount === 1) {
            swal('Error', 'Cannot disable all providers. At least one must remain enabled.', 'error');
            return;
        }

        try {
            setSaving(true);
            await axios.post(`/api/secure/lock/virtualaccounts/${AccessToken}/habukhan/secure`, {
                provider,
                enabled: newEnabled,
            });

            // Update local state
            setProviders((prev) => ({
                ...prev,
                [provider]: { ...prev[provider], enabled: newEnabled },
            }));

            swal('Success', `${provider.charAt(0).toUpperCase() + provider.slice(1)} has been ${newEnabled ? 'enabled' : 'disabled'}`, 'success');
        } catch (err) {
            console.error('Error toggling provider:', err);
            swal('Error', err.response?.data?.message || 'Failed to update provider status', 'error');
        } finally {
            setSaving(false);
        }
    };

    if (loading) {
        return (
            <Box display="flex" justifyContent="center" alignItems="center" minHeight="400px">
                <CircularProgress />
            </Box>
        );
    }

    return (
        <Grid container spacing={3}>
            <Grid item xs={12} md={8}>
                {error && <Alert severity="error" sx={{ mb: 3 }}>{error}</Alert>}

                <Card sx={{ p: 3 }}>
                    <Typography variant="h6" gutterBottom>
                        Enable/Disable Virtual Account Providers
                    </Typography>
                    <Typography variant="body2" color="text.secondary" sx={{ mb: 3 }}>
                        Control which virtual account providers are available to users. At least one provider must remain enabled.
                    </Typography>

                    <Stack spacing={2}>
                        <FormControlLabel
                            control={
                                <Switch
                                    checked={providers.xixapay?.enabled || false}
                                    onChange={() => handleToggle('xixapay')}
                                    disabled={saving}
                                    color="primary"
                                />
                            }
                            label={
                                <Box>
                                    <Typography variant="subtitle1">Xixapay (Palmpay)</Typography>
                                    <Typography variant="caption" color="text.secondary">
                                        Palmpay accounts via Xixapay
                                    </Typography>
                                </Box>
                            }
                        />

                        <FormControlLabel
                            control={
                                <Switch
                                    checked={providers.monnify?.enabled || false}
                                    onChange={() => handleToggle('monnify')}
                                    disabled={saving}
                                    color="primary"
                                />
                            }
                            label={
                                <Box>
                                    <Typography variant="subtitle1">Monnify (Moniepoint)</Typography>
                                    <Typography variant="caption" color="text.secondary">
                                        Moniepoint/Sterling accounts via Monnify
                                    </Typography>
                                </Box>
                            }
                        />

                        <FormControlLabel
                            control={
                                <Switch
                                    checked={providers.wema?.enabled || false}
                                    onChange={() => handleToggle('wema')}
                                    disabled={saving}
                                    color="primary"
                                />
                            }
                            label={
                                <Box>
                                    <Typography variant="subtitle1">Paystack (Wema)</Typography>
                                    <Typography variant="caption" color="text.secondary">
                                        Dedicated Wema Bank account via Paystack
                                    </Typography>
                                </Box>
                            }
                        />
                    </Stack>

                    <Alert severity="info" sx={{ mt: 3 }}>
                        <Typography variant="body2">
                            <strong>Note:</strong> Changes take effect immediately. Disabled providers will not appear in the Fund Wallet screen.
                        </Typography>
                    </Alert>
                </Card>
            </Grid>
        </Grid>
    );
}
