// @mui
import { Container } from '@mui/material';
import { PATH_ADMIN } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import VirtualAccountLock from '../../sections/admin/VirtualAccountLock';
import useAuth from '../../hooks/useAuth';

export default function VirtualAccountsLock() {
    const { themeStretch } = useSettings();
    const { setting } = useAuth();

    return (
        <Page title="Admin || Virtual Account Lock/Unlock">
            <Container maxWidth={themeStretch ? false : 'lg'}>
                <HeaderBreadcrumbs
                    heading={'Virtual Account Lock/Unlock'}
                    links={[
                        { name: 'Dashboard', href: PATH_ADMIN.root },
                        { name: 'Lock/Unlock Virtual Accounts' },
                    ]}
                />

                <VirtualAccountLock setting={setting?.setting} />
            </Container>
        </Page>
    );
}
